@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@300&family=Roboto+Mono:wght@300&display=swap');

:root {
  --book-width: 320px;
  --book-height: 500px;
}


* {
  margin: 0;
  padding: 0;
}


body {
  background: #525252;
  font-family: 'Roboto Mono', monospace;
}


.heading {
  font-size: 3rem;
  text-align: center;
  margin: 2rem;
  color: white;
}


.container {
  margin-bottom: 2rem; 
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 1rem;
  row-gap: 2rem;
}


.book {
  position: relative;
  max-width: var(--book-width);
  max-height: var(--book-height);
}


.cover-image {
  width: var(--book-width);
  height: var(--book-height);
  box-shadow: 0 -2px 10px rgba(0, 0, 0, 1);
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
  color: white;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  visibility: hidden;
}

.book:hover .overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  visibility: visible;
  animation: fade 0.35s;  /*https://stackoverflow.com/a/38772924*/
}

@keyframes fade {
  0% {
      opacity: 0;
  }

  100% {
      opacity: 1;
  }
}


.overlay .details {
  /* display: flex;
  flex-direction: column; */
  width: var(--book-width);
  height: var(--book-height);
  position: relative;
}

.rank {

}

.description {
  text-align: center;
  font-size: 1.2rem;
}

.rank {
  position: absolute;
  left: 1rem;
  top: 1.5rem;
  font-size: 1.3rem;
  color: grey;
}


.author {
  position: absolute;
  bottom: 2rem;
  left: 1rem;
}



.attribution {
  display: flex;
  justify-content: center;
}


.attribution img {
  /* transform: scale(0.9); */
  border-radius: 0.3rem;
}